"use client";

import { useState } from "react";
import { useForm } from "react-hook-form";
import { zodResolver } from "@hookform/resolvers/zod";
import { z } from "zod";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Alert, AlertDescription } from "@/components/ui/alert";
import { Textarea } from "@/components/ui/textarea";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { Calendar } from "@/components/ui/calendar";

const propertySchema = z.object({
  title: z.string().min(1, "El título es requerido"),
  description: z.string().min(10, "La descripción debe tener al menos 10 caracteres"),
  address: z.string().min(1, "La dirección es requerida"),
  area: z.string().min(1, "La zona es requerida"),
  propertyType: z.enum(['apartment', 'house', 'studio', 'room']),
  bedrooms: z.number().min(0, "Número de habitaciones inválido"),
  bathrooms: z.number().min(0, "Número de baños inválido"),
  size: z.number().min(1, "El tamaño debe ser mayor a 0"),
  furnished: z.boolean(),
  petFriendly: z.boolean(),
  smokingAllowed: z.boolean(),
  amenities: z.string().optional(),
  ownerName: z.string().min(1, "Nombre del propietario es requerido"),
  ownerEmail: z.string().email("Email del propietario inválido"),
  ownerPhone: z.string().min(10, "Teléfono del propietario inválido"),
  minIncome: z.number().min(0, "Ingreso mínimo requerido inválido"),
  minRating: z.number().min(0).max(5, "Rating mínimo debe estar entre 0 y 5"),
  availableFrom: z.string().min(1, "Fecha de disponibilidad requerida"),
  leaseDuration: z.number().min(1, "Duración del contrato debe ser mayor a 0"),
  winterPrice: z.number().min(0, "Precio de invierno debe ser positivo"),
  summerPrice: z.number().min(0, "Precio de verano debe ser positivo"),
});

type PropertyFormData = z.infer<typeof propertySchema>;

export default function NewPropertyPage() {
  const [step, setStep] = useState(1);
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [submitError, setSubmitError] = useState<string | null>(null);
  const [submitSuccess, setSubmitSuccess] = useState<string | null>(null);
  const [propertyId, setPropertyId] = useState<string | null>(null);
  const [selectedWinterDates, setSelectedWinterDates] = useState<Date[]>([]);
  const [selectedSummerDates, setSelectedSummerDates] = useState<Date[]>([]);

  const { register, handleSubmit, formState: { errors }, watch, setValue } = useForm<PropertyFormData>({
    resolver: zodResolver(propertySchema),
    defaultValues: {
      furnished: false,
      petFriendly: false,
      smokingAllowed: false,
      propertyType: 'apartment',
      minRating: 4.0,
      leaseDuration: 12,
    }
  });

  const watchedValues = watch();

  const onSubmit = async (data: PropertyFormData) => {
    setIsSubmitting(true);
    setSubmitError(null);
    setSubmitSuccess(null);

    try {
      // Step 1: Create property listing
      const propertyData = {
        ...data,
        ownerId: `owner_${Date.now()}`,
        amenities: data.amenities ? data.amenities.split(',').map(a => a.trim()) : [],
        images: [], // Will be handled in media upload step
        videos: [], // Will be handled in media upload step
      };

      const listResponse = await fetch('/api/property/list', {
        method: "POST",
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(propertyData),
      });

      const listResult = await listResponse.json();

      if (!listResponse.ok) {
        throw new Error(listResult.error || "Error al publicar la propiedad");
      }

      setPropertyId(listResult.propertyId);
      setSubmitSuccess(listResult.message);
      setStep(2); // Move to calendar configuration step

    } catch (error) {
      console.error('Property listing error:', error);
      setSubmitError(error instanceof Error ? error.message : "Error desconocido");
    } finally {
      setIsSubmitting(false);
    }
  };

  const handleCalendarSetup = async () => {
    if (!propertyId) return;

    setIsSubmitting(true);
    setSubmitError(null);

    try {
      const calendarData = {
        winter_available: [
          watchedValues.availableFrom,
          new Date(new Date().getFullYear(), 5, 30).toISOString().split('T')[0] // June 30
        ],
        summer_available: [
          new Date(new Date().getFullYear(), 6, 1).toISOString().split('T')[0], // July 1
          new Date(new Date().getFullYear(), 7, 31).toISOString().split('T')[0] // August 31
        ],
        pricing: {
          winter: watchedValues.winterPrice,
          summer: watchedValues.summerPrice
        }
      };

      const calendarResponse = await fetch(`/api/property/calendar/${propertyId}`, {
        method: "PUT",
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(calendarData),
      });

      const calendarResult = await calendarResponse.json();

      if (!calendarResponse.ok) {
        throw new Error(calendarResult.error || "Error al configurar el calendario");
      }

      setSubmitSuccess("¡Propiedad publicada exitosamente con calendario configurado!");
      setTimeout(() => {
        window.location.href = '/';
      }, 3000);

    } catch (error) {
      console.error('Calendar setup error:', error);
      setSubmitError(error instanceof Error ? error.message : "Error en la configuración del calendario");
    } finally {
      setIsSubmitting(false);
    }
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-primary/5 to-secondary/5 py-12">
      <div className="max-w-4xl mx-auto px-6">
        <div className="text-center mb-8">
          <h1 className="text-4xl font-bold text-secondary mb-4">Publicar Propiedad</h1>
          <p className="text-muted-foreground text-lg">
            Complete la información de su propiedad para conectar con inquilinos verificados
          </p>
        </div>

        {step === 1 && (
          <Card>
            <CardHeader>
              <CardTitle className="text-2xl text-center">Información de la Propiedad</CardTitle>
            </CardHeader>
            <CardContent>
              <form onSubmit={handleSubmit(onSubmit)} className="space-y-8">
                <Tabs defaultValue="basic" className="w-full">
                  <TabsList className="grid w-full grid-cols-4">
                    <TabsTrigger value="basic">Básico</TabsTrigger>
                    <TabsTrigger value="details">Detalles</TabsTrigger>
                    <TabsTrigger value="owner">Propietario</TabsTrigger>
                    <TabsTrigger value="requirements">Requisitos</TabsTrigger>
                  </TabsList>

                  <TabsContent value="basic" className="space-y-6">
                    <div>
                      <Label htmlFor="title">Título de la Propiedad</Label>
                      <Input 
                        id="title"
                        {...register("title")} 
                        className="mt-1"
                        placeholder="Ej: Apartamento Moderno en Centro"
                      />
                      {errors.title && <p className="text-red-500 text-sm mt-1">{errors.title.message}</p>}
                    </div>

                    <div>
                      <Label htmlFor="description">Descripción</Label>
                      <Textarea 
                        id="description"
                        {...register("description")} 
                        className="mt-1"
                        placeholder="Describe tu propiedad, sus características y ubicación..."
                        rows={4}
                      />
                      {errors.description && <p className="text-red-500 text-sm mt-1">{errors.description.message}</p>}
                    </div>

                    <div className="grid md:grid-cols-2 gap-4">
                      <div>
                        <Label htmlFor="address">Dirección</Label>
                        <Input 
                          id="address"
                          {...register("address")} 
                          className="mt-1"
                          placeholder="Calle Gran Vía 45, Madrid"
                        />
                        {errors.address && <p className="text-red-500 text-sm mt-1">{errors.address.message}</p>}
                      </div>

                      <div>
                        <Label htmlFor="area">Zona</Label>
                        <Input 
                          id="area"
                          {...register("area")} 
                          className="mt-1"
                          placeholder="Centro, Salamanca, Chamberí..."
                        />
                        {errors.area && <p className="text-red-500 text-sm mt-1">{errors.area.message}</p>}
                      </div>
                    </div>

                    <div>
                      <Label htmlFor="propertyType">Tipo de Propiedad</Label>
                      <Select onValueChange={(value) => setValue("propertyType", value as any)}>
                        <SelectTrigger className="mt-1">
                          <SelectValue placeholder="Selecciona el tipo" />
                        </SelectTrigger>
                        <SelectContent>
                          <SelectItem value="apartment">Apartamento</SelectItem>
                          <SelectItem value="house">Casa</SelectItem>
                          <SelectItem value="studio">Estudio</SelectItem>
                          <SelectItem value="room">Habitación</SelectItem>
                        </SelectContent>
                      </Select>
                      {errors.propertyType && <p className="text-red-500 text-sm mt-1">{errors.propertyType.message}</p>}
                    </div>
                  </TabsContent>

                  <TabsContent value="details" className="space-y-6">
                    <div className="grid md:grid-cols-3 gap-4">
                      <div>
                        <Label htmlFor="bedrooms">Habitaciones</Label>
                        <Input 
                          id="bedrooms"
                          type="number"
                          {...register("bedrooms", { valueAsNumber: true })} 
                          className="mt-1"
                          placeholder="2"
                        />
                        {errors.bedrooms && <p className="text-red-500 text-sm mt-1">{errors.bedrooms.message}</p>}
                      </div>

                      <div>
                        <Label htmlFor="bathrooms">Baños</Label>
                        <Input 
                          id="bathrooms"
                          type="number"
                          {...register("bathrooms", { valueAsNumber: true })} 
                          className="mt-1"
                          placeholder="1"
                        />
                        {errors.bathrooms && <p className="text-red-500 text-sm mt-1">{errors.bathrooms.message}</p>}
                      </div>

                      <div>
                        <Label htmlFor="size">Tamaño (m²)</Label>
                        <Input 
                          id="size"
                          type="number"
                          {...register("size", { valueAsNumber: true })} 
                          className="mt-1"
                          placeholder="75"
                        />
                        {errors.size && <p className="text-red-500 text-sm mt-1">{errors.size.message}</p>}
                      </div>
                    </div>

                    <div>
                      <Label htmlFor="amenities">Comodidades (separadas por comas)</Label>
                      <Textarea 
                        id="amenities"
                        {...register("amenities")} 
                        className="mt-1"
                        placeholder="WiFi, Aire Acondicionado, Calefacción, Ascensor, Garaje"
                        rows={3}
                      />
                    </div>

                    <div className="grid md:grid-cols-3 gap-4">
                      <div className="flex items-center space-x-2">
                        <input 
                          type="checkbox" 
                          id="furnished"
                          {...register("furnished")}
                          className="rounded border-gray-300"
                        />
                        <Label htmlFor="furnished">Amueblado</Label>
                      </div>

                      <div className="flex items-center space-x-2">
                        <input 
                          type="checkbox" 
                          id="petFriendly"
                          {...register("petFriendly")}
                          className="rounded border-gray-300"
                        />
                        <Label htmlFor="petFriendly">Acepta mascotas</Label>
                      </div>

                      <div className="flex items-center space-x-2">
                        <input 
                          type="checkbox" 
                          id="smokingAllowed"
                          {...register("smokingAllowed")}
                          className="rounded border-gray-300"
                        />
                        <Label htmlFor="smokingAllowed">Permite fumar</Label>
                      </div>
                    </div>
                  </TabsContent>

                  <TabsContent value="owner" className="space-y-6">
                    <div className="grid md:grid-cols-2 gap-4">
                      <div>
                        <Label htmlFor="ownerName">Nombre del Propietario</Label>
                        <Input 
                          id="ownerName"
                          {...register("ownerName")} 
                          className="mt-1"
                          placeholder="Ana Martínez"
                        />
                        {errors.ownerName && <p className="text-red-500 text-sm mt-1">{errors.ownerName.message}</p>}
                      </div>

                      <div>
                        <Label htmlFor="ownerPhone">Teléfono</Label>
                        <Input 
                          id="ownerPhone"
                          {...register("ownerPhone")} 
                          className="mt-1"
                          placeholder="+34 654 321 987"
                        />
                        {errors.ownerPhone && <p className="text-red-500 text-sm mt-1">{errors.ownerPhone.message}</p>}
                      </div>
                    </div>

                    <div>
                      <Label htmlFor="ownerEmail">Email del Propietario</Label>
                      <Input 
                        id="ownerEmail"
                        type="email"
                        {...register("ownerEmail")} 
                        className="mt-1"
                        placeholder="ana.martinez@email.com"
                      />
                      {errors.ownerEmail && <p className="text-red-500 text-sm mt-1">{errors.ownerEmail.message}</p>}
                    </div>
                  </TabsContent>

                  <TabsContent value="requirements" className="space-y-6">
                    <div className="grid md:grid-cols-2 gap-4">
                      <div>
                        <Label htmlFor="minIncome">Ingresos Mínimos Requeridos (€)</Label>
                        <Input 
                          id="minIncome"
                          type="number"
                          {...register("minIncome", { valueAsNumber: true })} 
                          className="mt-1"
                          placeholder="2500"
                        />
                        {errors.minIncome && <p className="text-red-500 text-sm mt-1">{errors.minIncome.message}</p>}
                      </div>

                      <div>
                        <Label htmlFor="minRating">Rating Mínimo (1-5)</Label>
                        <Input 
                          id="minRating"
                          type="number"
                          step="0.1"
                          min="0"
                          max="5"
                          {...register("minRating", { valueAsNumber: true })} 
                          className="mt-1"
                          placeholder="4.0"
                        />
                        {errors.minRating && <p className="text-red-500 text-sm mt-1">{errors.minRating.message}</p>}
                      </div>
                    </div>

                    <div className="grid md:grid-cols-2 gap-4">
                      <div>
                        <Label htmlFor="availableFrom">Disponible desde</Label>
                        <Input 
                          id="availableFrom"
                          type="date"
                          {...register("availableFrom")} 
                          className="mt-1"
                        />
                        {errors.availableFrom && <p className="text-red-500 text-sm mt-1">{errors.availableFrom.message}</p>}
                      </div>

                      <div>
                        <Label htmlFor="leaseDuration">Duración del Contrato (meses)</Label>
                        <Input 
                          id="leaseDuration"
                          type="number"
                          {...register("leaseDuration", { valueAsNumber: true })} 
                          className="mt-1"
                          placeholder="12"
                        />
                        {errors.leaseDuration && <p className="text-red-500 text-sm mt-1">{errors.leaseDuration.message}</p>}
                      </div>
                    </div>

                    <div className="bg-primary/10 p-6 rounded-lg">
                      <h3 className="text-lg font-semibold mb-4">Precios Estacionales</h3>
                      <div className="grid md:grid-cols-2 gap-4">
                        <div>
                          <Label htmlFor="winterPrice">Precio Invierno (€/mes)</Label>
                          <Input 
                            id="winterPrice"
                            type="number"
                            {...register("winterPrice", { valueAsNumber: true })} 
                            className="mt-1"
                            placeholder="1200"
                          />
                          <p className="text-sm text-muted-foreground mt-1">Septiembre - Junio</p>
                          {errors.winterPrice && <p className="text-red-500 text-sm mt-1">{errors.winterPrice.message}</p>}
                        </div>

                        <div>
                          <Label htmlFor="summerPrice">Precio Verano (€/mes)</Label>
                          <Input 
                            id="summerPrice"
                            type="number"
                            {...register("summerPrice", { valueAsNumber: true })} 
                            className="mt-1"
                            placeholder="1800"
                          />
                          <p className="text-sm text-muted-foreground mt-1">Julio - Agosto</p>
                          {errors.summerPrice && <p className="text-red-500 text-sm mt-1">{errors.summerPrice.message}</p>}
                        </div>
                      </div>
                    </div>
                  </TabsContent>
                </Tabs>

                {submitError && (
                  <Alert className="border-red-200 bg-red-50">
                    <AlertDescription className="text-red-800">
                      {submitError}
                    </AlertDescription>
                  </Alert>
                )}

                {submitSuccess && (
                  <Alert className="border-green-200 bg-green-50">
                    <AlertDescription className="text-green-800">
                      {submitSuccess}
                    </AlertDescription>
                  </Alert>
                )}

                <Button 
                  type="submit" 
                  disabled={isSubmitting} 
                  className="w-full bg-primary hover:bg-primary/90 text-secondary font-semibold py-3"
                >
                  {isSubmitting ? "Publicando..." : "Publicar Propiedad"}
                </Button>
              </form>
            </CardContent>
          </Card>
        )}

        {step === 2 && (
          <Card>
            <CardHeader>
              <CardTitle className="text-2xl text-center">Configuración de Calendario</CardTitle>
            </CardHeader>
            <CardContent className="text-center space-y-6">
              <div className="bg-green-50 p-6 rounded-lg">
                <h3 className="text-xl font-semibold text-green-800 mb-2">¡Propiedad Creada!</h3>
                <p className="text-green-700 mb-4">
                  Su propiedad ha sido publicada exitosamente. Ahora configuraremos el calendario de disponibilidad.
                </p>
              </div>

              <div className="bg-primary/10 p-6 rounded-lg text-left">
                <h3 className="text-lg font-semibold mb-4">Configuración Automática del Calendario</h3>
                <div className="space-y-3">
                  <div className="flex justify-between items-center">
                    <span>Temporada de Invierno:</span>
                    <span className="font-semibold">Sep - Jun (€{watchedValues.winterPrice}/mes)</span>
                  </div>
                  <div className="flex justify-between items-center">
                    <span>Temporada de Verano:</span>
                    <span className="font-semibold">Jul - Ago (€{watchedValues.summerPrice}/mes)</span>
                  </div>
                  <div className="flex justify-between items-center">
                    <span>Disponible desde:</span>
                    <span className="font-semibold">{watchedValues.availableFrom}</span>
                  </div>
                </div>
              </div>

              {submitError && (
                <Alert className="border-red-200 bg-red-50">
                  <AlertDescription className="text-red-800">
                    {submitError}
                  </AlertDescription>
                </Alert>
              )}

              {submitSuccess && (
                <Alert className="border-green-200 bg-green-50">
                  <AlertDescription className="text-green-800">
                    {submitSuccess}
                  </AlertDescription>
                </Alert>
              )}

              <Button 
                onClick={handleCalendarSetup}
                disabled={isSubmitting}
                className="bg-primary hover:bg-primary/90 text-secondary font-semibold px-8 py-3"
              >
                {isSubmitting ? "Configurando..." : "Finalizar Publicación"}
              </Button>
            </CardContent>
          </Card>
        )}

        <div className="text-center mt-8">
          <Button 
            variant="ghost" 
            onClick={() => window.location.href = '/'}
            className="text-muted-foreground hover:text-secondary"
          >
            Volver al Inicio
          </Button>
        </div>
      </div>
    </div>
  );
}
